<template>
  <div id="todo-app">

    <header>
      <h2>记住 <br>
        <small class="text-muted">To Do</small>
      </h2>
    </header>

    <div id="todo-item-container">

      <!-- 已完成/未完成 to.do 的导航 -->
      <b-tabs>
        <b-tab active>
          <template #title>
            <b-icon-x-circle></b-icon-x-circle> 未完成
          </template>
          <!-- 未完成 to.do -->
          <div v-bind:class="{ loading: isGettingItems }">
            <div id="completeTodos">
              <b-list-group >

                <!-- to.do 列表 -->
                <b-list-group-item
                  class="todo-item"
                  v-for="(todoItem, index) in todoItems"
                  v-show="!todoItem.is_complete"
                  :key="todoItem.todo_id"
                >
                  <div v-show="todoItem !== editingItem">
                    <!-- to.do 完成/未完成 checkbox -->
                    <input
                      type="checkbox"
                      v-model="todoItem.is_complete"
                      v-on:click="toggleTodo(todoItem, index)"
                    >
                    <!-- to.do 内容 -->
                    <span class="todo-content" v-bind:class="{ complete: todoItem.is_complete }">
                      {{ todoItem.content }}
                    </span>
                    <!-- to.do 编辑/删除按钮 -->
                    <span class="todo-item-control">
                      <a
                        class="todo-item-edit"
                        href="#"
                        v-on:click.prevent="editTodo(todoItem)"
                      >
                        <b-icon-pencil></b-icon-pencil>
                      </a>
                      <a
                        class="todo-item-delete"
                        href="#"
                        v-on:click.prevent="removeItem(todoItem, index)"
                      >
                        <b-icon-trash></b-icon-trash>
                      </a>
                    </span>
                  </div>

                  <!-- 已完成 to.do 的编辑界面 -->
                  <div v-show="todoItem === editingItem">
                    <form class="form" role="form" v-on:submit.prevent>
                      <div class="form-group">
                        <input
                          class="form-control todoEditor"
                          type="text"
                          autocomplete="off"
                          v-focus="todoItem === editingItem"
                          v-model="todoItem.content"
                          v-on:keyup.enter="doneEdit(todoItem, index)"
                          v-on:keyup.esc="cancelEdit(todoItem)"
                        >
                      </div>

                      <!-- to.do 确定/取消 编辑 -->
                      <b-button
                        variant="success"
                        size="sm"
                        v-on:click.prevent="doneEdit(todoItem, index)"
                      >
                        <b-icon-check></b-icon-check> 确定
                      </b-button>
                      <b-button
                        variant="outline-danger"
                        size="sm"
                        v-on:click="cancelEdit(todoItem)"
                      >
                        <b-icon-x></b-icon-x> 取消
                      </b-button>
                    </form>
                  </div>
                </b-list-group-item><!-- to.do 列表结束 -->
              </b-list-group>
            </div>
          </div>
        </b-tab>
        <b-tab>
          <template #title>
            <b-icon-check-square></b-icon-check-square> 已完成
          </template>
          <!-- 已完成 to.do -->
          <div id="uncompletedTodos">
            <b-list-group>

              <!-- to.do 列表 -->
              <b-list-group-item
                class="todo-item"
                v-for="(todoItem, index) in todoItems"
                v-show="todoItem.is_complete"
                :key="todoItem.todo_id"
              >
                <div v-show="todoItem !== editingItem">
                  <input
                    type="checkbox"
                    v-model="todoItem.is_complete"
                    v-on:click="toggleTodo(todoItem, index)"
                  >
                  <span class="todo-content" v-bind:class="{ complete: todoItem.is_complete }">
                    {{ todoItem.content }}
                  </span>
                  <span class="todo-item-control">
                  <a
                    class="todo-item-edit"
                    href="#"
                    v-on:click.prevent="editTodo(todoItem)"
                  >
                    <b-icon-pencil></b-icon-pencil>
                  </a>
                  <a
                    class="todo-item-delete"
                    href="#"
                    v-on:click.prevent="removeItem(todoItem, index)"
                  >
                    <b-icon-trash></b-icon-trash>
                  </a>
                </span>
                </div>

                <!-- to.do 编辑界面 -->
                <div v-show="todoItem === editingItem">
                  <form class="form" role="form" v-on:submit.prevent>
                    <div class="form-group">
                      <input
                        class="form-control todoEditor"
                        type="text"
                        autocomplete="off"
                        v-focus="todoItem === editingItem"
                        v-model.trim="todoItem.content"
                        v-on:keyup.enter="doneEdit(todoItem, index)"
                        v-on:keyup.esc="cancelEdit(todoItem)"
                      >
                    </div>
                    <b-button
                      variant="success"
                      size="sm"
                      v-on:click.prevent="doneEdit(todoItem, index)"
                    >
                      <b-icon-check></b-icon-check> 确定
                    </b-button>
                    <b-button
                      variant="outline-danger"
                      size="sm"
                      v-on:click="cancelEdit(todoItem)"
                    >
                      <b-icon-x></b-icon-x> 取消
                    </b-button>
                  </form>
                </div>
              </b-list-group-item><!-- 未完成 to.do 列表结束 -->
            </b-list-group>
          </div>
        </b-tab>
      </b-tabs>
    </div><!-- to.do 容器结束 -->

    <!-- 添加新 to.do 界面 -->
    <div id="todo-add" class="mt-3">

      <!-- 显示表单的按钮 -->
      <div
        id="todo-add-btn"
        v-show="!isAddingSeen"
        v-on:click="showAddingForm"
      >
        <span><b-icon-plus></b-icon-plus> 添加一个事项</span>
      </div>

      <!-- 添加 to.do 的表单 -->
      <div id="todo-add-form" v-show="isAddingSeen">
        <form class="form" role="form" v-on:submit.prevent>
          <div class="form-group">
            <input
              class="form-control"
              id="inputNewTodo"
              type="text"
              placeholder="要做什么事？"
              autocomplete="off"
              maxlength="15"
              v-model.trim="newTodo.content"
              v-on:keyup.enter="addItem"
              v-on:keyup.esc="cancelAdd($event)"
              v-on:blur="cancelAdd($event)"
            >
          </div>
        </form>
        <b-button
          variant="success"
          id="submitNewTodo"
          :disabled="isAddingItem"
          v-on:click="addItem"
        >
          <b-icon icon="plus"></b-icon> 添加
        </b-button>
      </div>
    </div><!-- end of #to.do-add -->

    <!-- 错误信息 -->
    <b-alert
      variant="danger"
      :show="errorMsg.hasError"
      dismissible
    >
      <strong>发生错误！</strong>{{ errorMsg.text }}
    </b-alert>

  </div><!-- end of #to.do-app -->
</template>

<script>
import {generateId} from '@/helper/helper'

export default {

  name: 'todo-app',

  data: function() {

    return {

      todoItems: [
//          {
//            todo_id: 1,
//            user_id: 1,
//            content: '去买菜',
//            is_complete: false
//          }
      ],

      editingItem: null,

      beforeEditCache: '',

      newTodo: {
        todo_id: 0,
        content: '',
        is_complete: false
      },

      isGettingItems: false,

      isAddingItem: false,

      isAddingSeen: false,

      STORAGE_KEY: 'todo_local',

      errorMsg: {
        hasError: false,
        text: '',
        map: {
          // get
          101: '提交的用户ID不正确。',
          // add
          201: '找不到对应用户ID。',
          202: '无法添加该记录到数据库。',
          // edit
          301: '数据库中没有这个 todo。',
          302: '编辑 todo 失败。',
          // toggle
          401: '数据库中没有这个 todo。',
          402: '更改 todo 失败。',
          // remove
          501: '数据库中没有这个 todo。',
          502: '删除 todo 失败。',
          // common
          600: '提交的数据有误。',
          // no correct response
          'nores': '服务器发生错误。',
          // 提交了空 to.do
          'empty': '不能建立空事项',
          // other
          'other': ''
        }
      }

    }

  },

  computed: {

    isSignIned: function() {
      return this.$store.state.isSignIned;
    }

  },

  watch: {
    // to.do 发生变化，也就是被正确地更改后清除错误信息
    todoItems: {
      deep: true,
      handler: function() {
        this.dismissErrorMsg();
      }
    }
  },

  directives: {
    focus: {
      update: function (el, binding) {
        if (!binding.value) {
          return;
        }
        setTimeout(function () {
          el.focus();
        }, 0);
      }
    }
  },

  methods: {


    // 获取 todos
    getTodoItems: function() {

      if (this.isSignIned) {

        if (this.isGettingItems) {return}

        this.isGettingItems = true

        this.todoItems = []

        this.$store.commit('getTodo', {user_id: this.$store.state.currentUser.user_id})

        this.todoItems = this.$store.state.currentTodo

        this.isGettingItems = false

      } else {
        if (!window.localStorage.getItem(this.STORAGE_KEY)) {
          // 未登录且没有本地记录时插入两条记录
          const defaultLocalTodos = [
            {
              todo_id: generateId('_todoIdMax'),
              content: '在这里记录你的待办事项',
              is_complete: false,
            },
            {
              todo_id: generateId('_todoIdMax'),
              content: '登录后可以看到你自己的记录',
              is_complete: false,
            }
          ];
          window.localStorage.setItem(this.STORAGE_KEY, JSON.stringify(defaultLocalTodos));
          this.todoItems = defaultLocalTodos;
        } else {
          this.todoItems =  JSON.parse(localStorage.getItem(this.STORAGE_KEY)) || [];
        }
      }
    },

    // 添加 to.do
    addItem: function () {

      if (this.isAddingItem) {
        return
      }

      if (this.newTodo.content === '') {
        alert('不能建立空事项')
        this.showErrorMsg('empty');
        document.getElementById('inputNewTodo').focus()
        return
      }

      if (this.isSignIned) {

        this.isAddingItem = true

        this.$store.commit('addTodo', {
          content: this.newTodo.content,
        })

        this.getTodoItems()

        // this.isAddingItem = false

      } else {
        this.newTodo.todo_id = generateId('_todoIdMax')
        this.todoItems.push(this.newTodo)
        window.localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.todoItems))
      }

      this.newTodo = {
        todo_id: 0,
        content: '',
        is_complete: false
      }
      this.isAddingItem = false
      this.isAddingSeen = false
    },

    cancelAdd: function (event) {
      this.dismissErrorMsg()
      if (!this.isAddingSeen) {return}
      if (event && event.type === 'blur') {
        if (this.newTodo.content === '') {
          this.showErrorMsg('empty')
          this.isAddingSeen = false
        }
      } else {
        this.isAddingSeen = false
      }
    },

    showAddingForm: function () {
      this.isAddingSeen = true
      setTimeout(function () {
        document.getElementById('inputNewTodo').focus();
      }, 0)
    },

    // 编辑
    editTodo: function (todo) {
      this.beforeEditCache = todo.content
      this.editingItem = todo
    },

    cancelEdit: function (todo) {
      todo.content = this.beforeEditCache
      this.editingItem = null
    },

    doneEdit: function (todo, index) {

      if (!this.editingItem) {
        return
      }

      if (!todo.content) {
        this.removeItem(todo, index)
        return
      }

      if (this.isSignIned) {

        this.$store.commit('editTodo', {
          todo_id: this.editingItem.todo_id,
          content: this.editingItem.content.trim(),
        })

        this.editingItem = null

        this.getTodoItems()

      } else {
        todo.content = todo.content.trim();
        localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.todoItems))
        this.editingItem = null
      }
    },

    // 完成/取消完成 to.do
    toggleTodo: function(todo) {

      if (this.isSignIned) {

        this.$store.commit('toggleTodo', {
          todo_id: todo.todo_id,
          is_complete: todo.is_complete
        })

        this.editingItem = null

        this.getTodoItems()

      } else {
        todo.is_complete = !todo.is_complete
        window.localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.todoItems));
      }
    },

    // 删除 to.do
    removeItem: function (todo, index) {

      if (this.isSignIned) {

        this.$store.commit('removeTodo', {
          todo_id: todo.todo_id
        })

        this.getTodoItems()

      } else {
        this.todoItems.splice(index, 1);
        window.localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.todoItems));
      }
    },

    showErrorMsg: function(errorCode, error) {
      if (!errorCode) {return;}
      this.errorMsg.hasError = true;
      if (error) {
        this.errorMsg.text = error.toString();
      } else {
        this.errorMsg.text = this.errorMsg.map[errorCode] + '(错误码：' + errorCode + ')';
      }
    },

    dismissErrorMsg: function() {
      this.errorMsg.hasError = false
    }
  },

  created: function() {
    this.getTodoItems()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

#todo-app {

  label {
    cursor: pointer;
  }

  .todo-item {
    .todo-content.complete {
      text-decoration: line-through;
    }
  }

  .todo-item-control {
    float: right;
    .todo-item-edit {
      margin-right: 10px;
    }
    a:hover {
      text-decoration: none;
    }
  }

}

#todo-add {

  margin-bottom: 30px;

  #todo-add-btn {
    span:first-of-type {
      cursor: pointer;
    }
  }

  #todo-add-form {
    label {
      cursor: pointer;
    }
  }
}

.nav-tabs {
  border-bottom: none;
}

