// let id = 0

function generateId(key) {
  let id = 0
  if (window.localStorage.getItem(key) === null) {
    window.localStorage.setItem(key, JSON.stringify(0))
  } else {
    id = Number.parseInt(JSON.parse(window.localStorage.getItem(key)))
  }
  id += 1
  window.localStorage.setItem(key, JSON.stringify(id))
  return id
}

export {generateId}
