<template>
  <b-navbar
    id="navbar-top"
    class="navbar navbar-light bg-light"
  >
    <b-navbar-brand>
      <a href="#">
        <img src="./assets/logo_black.png" alt="WeirdFishes">
      </a>
    </b-navbar-brand>
    <b-collapse id="nav-collapse" is-nav>
      <!-- normal nav btns -->
      <b-navbar-nav>
        <!-- dynamic router nav btn -->
          <b-nav-item
            v-for="navbtn in navbtns"
            :key="navbtn.text"
          >
            <router-link
              v-bind:to="navbtn.path"
              exact
            >
              <b-icon :icon="navbtn.iconName"></b-icon> {{ navbtn.text }}
            </router-link>
          </b-nav-item>
        <!-- static origin nav btn -->
        <b-nav-item
          v-for="navbtn in staticNavbtns"
          :key="navbtn.text"
        >
          <a
            v-bind:href="navbtn.path"
            v-bind:target="navbtn.newWindow ? '_blank' : '_self'"
          >
            <b-icon :icon="navbtn.iconName"></b-icon> {{ navbtn.text }}
          </a>
        </b-nav-item>
      </b-navbar-nav>
      <!-- login management -->
      <b-navbar-nav class="ml-auto"
        v-show="!isSignIned"
      >
        <b-nav-item
          v-for="btn in loginbtns"
          :key="btn.text"
          v-showModal="btn"
          @click="showModal(btn.modalID)"
        >
          <a :href="btn.href">
            <b-icon :icon="btn.iconName"></b-icon> {{ btn.text }}
          </a>
        </b-nav-item>
      </b-navbar-nav> <!-- End of login management -->
      <!-- logout management -->
      <b-navbar-nav class="ml-auto"
        v-show="isSignIned"
      >
        <b-nav-item-dropdown
          :text="'欢迎，' + $store.state.currentUser.user_name"
          right
        >
          <b-dropdown-item
            href="#"
            v-on:click.prevent="signOut"
          >
            <b-icon-box-arrow-right></b-icon-box-arrow-right>
            <span>登出</span>
          </b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav> <!-- End of logout management -->
    </b-collapse> <!-- End of .navbar-collapse -->
  </b-navbar>
</template>

<script>
export default {
  name: 'navbar-top',
  data: function() {
    return {

      navbtns: [
        {
          text: '主页',
          path: '/',
          iconName: 'house'
        },
        // {
        //   text: '看点新闻',
        //   path: '/news',
        //   iconName: 'newspaper'
        // }
      ],

      staticNavbtns: [
        {
          text: '简历',
          path: '/resume',
          iconName: 'person-square',
          newWindow: true
        }
      ],

      loginbtns: [
        {
          text: '登录',
          href: 'javascript:void(0);',
          iconName: 'box-arrow-in-left',
          modalID: 'sign-in-modal'
        },
        {
          text: '注册',
          href: 'javascript:void(0);',
          iconName: 'box-arrow-in-down',
          modalID: 'sign-up-modal'
        }
      ]

    }
  },

  computed: {
    isSignIned: function() {
      return this.$store.state.isSignIned;
    }
  },

  directives: {
    showModal: {
      inserted: function(el, binding) {
        let id = binding.value.modalID;
        el.setAttribute('data-toggle', 'modal');
        el.setAttribute('data-target', id);
      }
    }
  },

  methods: {

    showModal(id) {
      this.$bvModal.show(id)
    },

    signOut() {
      this.$store.commit('signOut')
      location.reload(true)
    },

    // goTo: function(path) {
    //   this.$router.push(path);
    // }
  }
}
</script>

