<template>

  <div id="chatboard-app" v-cloak>

    <header>
      <h2>留言 <br>
        <small class="text-muted">To Talk</small>
      </h2>
    </header>

    <main id="msg-box">

      <!-- 留言输入区 -->
      <form
        id="msg-form"
        v-on:submit.stop.prevent
        v-show="isSignIned"
      >
        <div>
          <textarea
            id="input-content"
            v-model.trim="newMsgContent"
          ></textarea>
        </div>
        <div class="msg-form-control">
          <p>
            <span>
              {{ msgLengthLeft >= 0 ? '还能输入' : '已超出' }}
            </span>
            <strong
              class="counter-result-text"
              v-bind:style="{ color: msgLengthLeft >= 0 ? '' : '#FF6600' }"
            >
              {{ msgLengthLeft >= 0 ? msgLengthLeft : (msgLengthLeft * -1) }}
            </strong>
            <span>个字</span>
            <b-button
              variant="success"
              id="msg-submit-btn"
              title="快捷键 Ctrl+Enter"
              :disabled="isSubmitting"
              v-on:click.prevent="postMessage"
            >
              <b-icon icon="check"></b-icon> 发布
            </b-button>
          </p>
        </div>
        <input type="hidden" name="token" value="">
      </form>

      <!-- 未登录提醒文本 -->
      <p
        class="text-center"
        id="chatboard-unsignined-alert"
        v-show="!isSignIned"
      >
        留言功能目前只对注册用户开放
      </p>

      <!-- 留言 -->
      <main class="msg-list-wrap">
        <b-tabs>
          <b-tab active>
            <template #title>
              <b-icon-mic></b-icon-mic> 大家在说
            </template>
          </b-tab>
        </b-tabs>
        <ul class="msg-list" v-bind:class="{ loading: isGettingItems }">
          <!-- 留言 list -->
          <li
            v-for="(message, index) in messages"
            :key="message.msg_id"
            v-on:mouseenter="toggleManagementBtns($event)"
            v-on:mouseleave="toggleManagementBtns($event)"
          >
            <aside class="user-avatar">
              <img :src="getAvatarURL(message)" :alt="message.avatar">
            </aside>
            <main>
              <header class="user-name">
                <a href="javascript:void(0);">
                  {{ message.user_name }}
                </a>:
              </header>
              <main class="content">
                <div class="msg-content">{{ message.content }}</div>
              </main>
              <footer>
                <div class="times">
                  <span>
                    {{ message.post_time }}
                  </span>
                  <span
                    class="msg-management"
                    v-if="message.user_id === $store.state.currentUser.user_id || $store.state.currentUser.user_level === 0"
                  >
                    <a
                      class="msg-del-btn"
                      v-on:click.prevent="deleteMessage(message, index)"
                    >删除</a>
                  </span>
                </div>
              </footer>
            </main>

          </li>
        </ul>
      </main><!-- 留言结束 -->
    </main>

    <!-- 留言页码 -->
    <nav class="page-nav">
<!--      <b-pagination-->
<!--        v-model="paginationData.currentPage"-->
<!--        :per-page="paginationData.display"-->
<!--        :total-rows="$store.state.allMsgLength"-->
<!--      >-->
<!--      </b-pagination>-->
      <ul class="pagination pagination-sm">

        <!-- 上一页 -->
        <li
          v-show="(paginationData.pages > 1) && (paginationData.currentPage > 0)"
          v-on:click.prevent="getMessages(paginationData.currentPage - 1)"
        >
          <a class="page-link">
            <span aria-hidden="true">
              &laquo;
            </span>
          </a>
        </li>

        <!-- 页码 -->
        <li
          v-for="(page, index) in paginationData.pages"
          :key="index"
          :class="index === paginationData.currentPage ? 'active' : ''"
          v-on:click.prevent="getMessages(index)"
        >
          <a class="page-link">
            {{ index + 1 }}
          </a>
        </li>

        <!-- 下一页 -->
        <li
          v-show="(paginationData.pages > 1) && (paginationData.currentPage < (paginationData.pages - 1))"
          v-on:click.prevent="getMessages(paginationData.currentPage + 1)"
        >
          <a class="page-link">
            <span aria-hidden="true">
              &raquo;
            </span>
          </a>
        </li>

      </ul>
    </nav>

    <!-- 错误信息 -->
    <b-alert :show="errorMsg.hasError">
      <strong>发生错误！</strong>{{ errorMsg.text }}
    </b-alert>

  </div>

</template>

<script>

  export default {

    name: 'chatboard-app',

    data: function() {

      return {
        // 留言队列
        messages: [
//          {
//            msg_id: 2,
//            user_id: 1,
//            user_name: 'CJ',
//            content: 'some text',
//            post_time: '2017-01-01 23:00:00'
//            avatar: 'avatar',
//          }
        ],

        // textarea 中的内容
        newMsgContent: '',

        // 留言字数限制
        msgLengthLimit: 140,

        // 留言导航数据
        paginationData: {
          display: 5,       // 每次显示多少条留言
          currentPage: 0,    // 记录当前留言页码，初始值为0，即从第1页开始显示
          pages: 0           // 留言页数
        },

        isGettingItems: false,

        isSubmitting: false,

        deleteTimer: null,

        errorMsg: {
          hasError: false,
          text: '',
          map: {
            // add
            101: '找不到对应用户ID。',
            102: '无法添加该留言到数据库。',
            // remove
            201: '数据库中没有这条留言。',
            202: '删除留言失败。',
            // common
            600: '提交的数据有误。',
            // no correct response
            'nores': '服务器发生错误。',
            // 提交了空 msg
            'empty': '留言内容不能为空',
            'textoverflow': '留言超出了限制字数',
            'notsignined': '留言功能目前只对注册用户开放',
            // other
            'other': ''
          }
        }

      }

    },

    computed: {

      // 获取剩余可输入留言长度
      msgLengthLeft: function () {
        return this.msgLengthLimit - this.newMsgContent.length;
      },

      // 保存当前输入的留言长度
      msgCurrentLength: function() {
        return this.newMsgContent.length;
      },

      isSignIned: function() {
        return this.$store.state.isSignIned
      }

    },

    watch: {
      // messages 发生变化，也就是被正确地更改后清除错误信息
      messages: {
        deep: true,
        handler: function() {
          this.dismissErrorMsg();
        }
      }
    },

    methods: {

      // 获取
      getMessages: function (index) {

        // let index_ = index || 0

        // 如果要切换到的页码等于当前页码，就退出
        if (index === this.paginationData.currentPage) {
          return
        }

        this.isGettingItems = true

        let index_ = index || 0;

        this.$store.commit('getMsg', {
          // 计算数据库记录开始位置，位置=页码序号 * 每页显示留言数
          start: index_ * this.paginationData.display,
          display: this.paginationData.display
        })

        this.messages = this.$store.state.currentMsg

        // 更新当前页码
        this.paginationData.currentPage = index_
        // 更新页数
        this.paginationData.pages = Math.ceil(this.$store.state.allMsgLength / this.paginationData.display)

        this.isGettingItems = false

      },

      // 添加
      postMessage: function () {

        if (!this.isSignIned) {
          this.showErrorMsg('notsignined');
          return
        }

        if (this.isSubmitting) {
          return
        }

        let inputContent = document.querySelector('#input-content');

        if (this.newMsgContent === '') {
          this.showErrorMsg('empty');
          inputContent.focus();
          return;
        }

        if (this.msgCurrentLength > this.msgLengthLimit) {
          this.showErrorMsg('textoverflow');
          inputContent.focus();
          return;
        }

        this.isSubmitting = true

        this.$store.commit('addMsg', {
          content: this.newMsgContent
        })

        this.newMsgContent = ''

        this.isSubmitting = false

        this.getMessages()

      },

      // 删除
      deleteMessage: function (message) {

        this.dismissErrorMsg()

        // 检查是否登录
        if (!this.isSignIned) {
          return
        }

        if (this.deleteTimer) {
          clearTimeout(this.deleteTimer);
        }
        // 在 300 毫秒内的多次点击只有最后一次有效
        this.deleteTimer = setTimeout(() => {

          this.$store.commit('removeMsg', {
            msg_id: message.msg_id
          })

          if (this.$store.state.errorState.removeMsgWrongMsgID) {
            this.showErrorMsg(201)
          } else if (this.$store.state.errorState.removeMsgWrongUserID) {
            this.showErrorMsg(101)
          } else {
            this.getMessages()
          }

        }, 300);

      },

      // 鼠标移入移出时切换显示消息管理（编辑、删除）按钮
      toggleManagementBtns: function (event) {
        if (!this.isSignIned) {return}
        let msgManagement = event.target.querySelector('.msg-management')
        if (!msgManagement) {return}
        switch (event.type) {
          case 'mouseenter':
            msgManagement.classList.add('hover');
            break;
          case 'mouseleave':
            msgManagement.classList.remove('hover');
            break;
          default:
            break;
        }
      },

      getAvatarURL: function (message) {
        return require('@/assets/avatar/' + message.avatar + '_50.jpg')
      },

      showErrorMsg: function(errorCode, error) {
        if (!errorCode) {return;}
        this.errorMsg.hasError = true;
        if (error) {
          this.errorMsg.text = error.toString();
        } else {
          this.errorMsg.text = this.errorMsg.map[errorCode] + '(错误码：' + errorCode + ')';
        }
      },

      dismissErrorMsg: function() {
        this.errorMsg.hasError = false
      }

    },

    created: function() {
      this.getMessages();
    }
  };
</script>

