<template>
  <div id="tormb-app" v-cloak>

    <header>
      <h2>纪念 <br>
        <small class="text-muted">To Remember</small>
      </h2>
    </header>

    <b-form-group id="tormb-item-container">
      <b-list-group v-bind:class="{ loading: isGettingItems }">

        <!-- 纪念日 list -->
        <b-list-group-item
          v-for="(tormbItem, index) in tormbItems"
          :key="tormbItem.tormb_id"
        >
<!--          <div v-show="tormbItem !== editingItem">-->
          <div v-show="tormbItem !== editingItem">
            <span class="tormb-item-addon">{{ getDaysDiff(tormbItem.date) === 0 ? "今天是" : "距离" }}</span>
            <span class="tormb-item-content">{{ tormbItem.content }}</span>
            <span class="tormb-item-til" v-if="getDaysDiff(tormbItem.date) === 0"></span>
            <span class="tormb-item-til" v-else-if="getDaysDiff(tormbItem.date) < 0">已经过去</span>
            <span class="tormb-item-til" v-else>还有</span>
            <span class="tormb-item-timediff" v-if="getDaysDiff(tormbItem.date) === 0"></span>
            <span class="tormb-item-timediff" v-else-if="getDaysDiff(tormbItem.date) < 0">{{ getDaysDiff(tormbItem.date) * -1 }}</span>
            <span class="tormb-item-timediff" v-else>{{ getDaysDiff(tormbItem.date) }}</span>
            <span class="tormb-item-timeunit">{{ getDaysDiff(tormbItem.date) === 0 ? "" : "天" }}</span>

            <!-- 纪念日删除/编辑按钮 -->
            <span class="tormb-item-control">
              <a class="tormb-item-edit" v-on:click.prevent="editTormb(tormbItem)">
                <b-icon-pencil></b-icon-pencil>
              </a>
              <a class="tormb-item-delete" v-on:click.prevent="removeItem(index)">
                <b-icon-trash></b-icon-trash>
              </a>
            </span>
          </div>

          <!-- 纪念日编辑界面 -->
          <b-form-group v-show="tormbItem === editingItem">
            <b-form role="form" v-on:submit.prevent>

              <!-- 内容 -->
              <b-form-group>
                <b-form-input
                  class="tormb-content"
                  placeholder="要记住什么日子？"
                  v-model="tormbItem.content"
                  trim
                ></b-form-input>
              </b-form-group>

              <!-- 日期 -->
              <b-form-group>
                <b-form-group>
                  <b-form-datepicker
                    v-model="tormbItem.date"
                    placeholder="日期"
                    autocomplete="off"
                    v-on:keyup.enter="addItem"
                    v-on:keyup.esc="cancelAdd"
                  >
                  </b-form-datepicker>
                </b-form-group>

                <!-- 每年提醒 -->
                <b-form-group>
                  <b-form-checkbox
                    v-model="tormbItem.is_annual"
                  >
                    每年提醒
                  </b-form-checkbox>
                </b-form-group>
              </b-form-group>

              <!-- 确定/取消按钮 -->
              <b-button
                variant="success"
                size="sm"
                v-on:click="doneEdit(tormbItem)"
              >
                <b-icon-check></b-icon-check> 确定
              </b-button>
              <b-button
                variant="outline-danger"
                size="sm"
                v-on:click="cancelEdit(tormbItem)"
              >
                <b-icon-x></b-icon-x> 取消
              </b-button>
            </b-form>
          </b-form-group>
        </b-list-group-item><!-- 结束当前纪念日 -->
      </b-list-group>
    </b-form-group><!-- 纪念日列表容器结束 -->

    <!-- 添加新纪念日界面 -->
    <div id="tormb-add">

      <!-- 显示新纪念日编辑器的按钮 -->
      <div
        id="tormb-add-btn"
        v-show="!isAddingSeen"
        v-on:click="showAddingForm"
      >
        <span><b-icon-plus></b-icon-plus> 添加一个日子</span>
      </div>

      <!-- 新纪念日编辑器表单 -->
      <div id="tormb-add-form" v-show="isAddingSeen">

        <b-form v-on:submit.prevent>

          <!-- 新纪念日内容 -->
          <b-form-group>
            <b-form-input
              id="inputNewTormbContent"
              placeholder="要记住什么日子？"
              autocomplete="off"
              v-model="newTormb.content"
              trim
              v-on:keyup.enter="moveFocus"
              v-on:keyup.esc="cancelAdd"
            ></b-form-input>
          </b-form-group>

          <!-- 新纪念日日期 -->
          <b-form-group>
            <b-form-datepicker
              v-model="newTormb.date"
              placeholder="日期"
              autocomplete="off"
              v-on:keyup.enter="addItem"
              v-on:keyup.esc="cancelAdd"
            >
            </b-form-datepicker>
          </b-form-group>

          <!-- 新纪念日是否每年提醒 -->
          <b-form-group>
            <b-form-checkbox
              v-model="newTormb.is_annual"
            >
              每年提醒
            </b-form-checkbox>
          </b-form-group>
        </b-form>

        <!-- 添加/取消新纪念日按钮 -->
        <b-button
          variant="success"
          size="sm"
          :disabled="isAddingItem"
          v-on:click="addItem"
        >
          <b-icon-plus></b-icon-plus> 添加
        </b-button>
        <b-button
          variant="outline-danger"
          size="sm"
          v-on:click="cancelAdd"
        >
          <b-icon-x></b-icon-x> 取消
        </b-button>
      </div>
    </div><!-- 添加新纪念日界面结束 -->

    <!-- 错误信息 -->
    <b-alert
      variant="danger"
      :show="errorMsg.hasError"
      dismissible
    >
      <strong>发生错误！</strong>{{ errorMsg.text }}
    </b-alert>

  </div><!-- 纪念日应用结束 -->

</template>

<script>
  import dayjs from 'dayjs'
  import {generateId} from '@/helper/helper'

  export default {

    name: 'tormb-app',

    data: function() {

      return {

        tormbItems: [
//          {
//            "tormb_id": 1,
//            "user_id": 1,
//            "content": '我的生日',
//            "date": "2018-01-13",
//            "is_annual": 1
//          }
        ],

        editingItem: null,

        beforeEditCache: {
          content: '',
          date: '',
          is_annual: false
        },

        newTormb: {
          tormb_id: 0,
          content: '',
          date: '',
          is_annual: false
        },

        isGettingItems: false,

        isAddingItem: false,

        isAddingSeen: false,

        STORAGE_KEY: 'tormb_local',

        errorMsg: {
          hasError: false,
          text: '',
          map: {
            // get
            101: '提交的用户ID不正确。',
            // add
            201: '找不到对应用户ID。',
            202: '无法添加该记录到数据库。',
            // edit
            301: '数据库中没有这个纪念日。',
            302: '编辑纪念日失败。',
            // recycle
            401: '数据库中没有这个纪念日',
            402: '自更新纪念日失败。',
            // remove
            501: '数据库中没有这个纪念日。',
            502: '删除纪念日失败。',
            // common
            600: '提交的数据有误。',
            // no correct response
            'nores': '服务器发生错误。',
            // 提交了空值
            'emptycontent': '没有输入纪念日',
            'emptydate': '没有输入日期',
            // 日期错误
            'invaliddate': '输入的日期不对，请检查（格式需为“2000-01-01”）',
            // other
            'other': ''
          }
        }

      }

    },

    computed: {

      isSignIned: function() {
        return this.$store.state.isSignIned;
      }

    },

    watch: {
      // 纪念日数据发生变化，也就是被正确地更改后清除错误信息
      tormbItems: {
        deep: true,
        handler: function() {
          this.dismissErrorMsg();
        }
      }
    },

    methods: {

      getTormbItems: function() {

        if (this.isSignIned) {

          if (this.isGettingItems) {return}

          this.isGettingItems = true

          this.tormbItems = []

          this.$store.commit('getTormb', {user_id: this.$store.state.currentUser.user_id})

          this.tormbItems = this.$store.state.currentTormb

          this.isGettingItems = false

        } else {
          if (!localStorage.getItem(this.STORAGE_KEY)) {
            // 未登录且没有本地记录时插入一条当天的记录
            let today = new Date()
            let YEAR = today.getFullYear()
            let MONTH = today.getMonth() + 1
            let DATE = today.getDate()
            let defaultLocalTormbs = [
              {
                tormb_id: generateId('_tormbIdMax'),
                content: YEAR + '年' + MONTH + '月' + DATE + '日',
                date: [YEAR, MONTH, DATE].join('-'),
                is_annual: false,
              },
              {
                tormb_id: generateId('_tormbIdMax'),
                content: '下一个国庆节',
                date: '2021-10-01',
                is_annual: true,
              }
            ];
            window.localStorage.setItem(this.STORAGE_KEY, JSON.stringify(defaultLocalTormbs))
            this.tormbItems = defaultLocalTormbs
          } else {
            this.tormbItems = JSON.parse(window.localStorage.getItem(this.STORAGE_KEY)) || []
          }
        }
      },

      addItem: function () {

        if (this.isAddingItem) {return}

        if (!this.newTormb.content) {
          this.showErrorMsg('emptycontent')
          document.getElementById('inputNewTormbContent').focus()
          return
        }

        if (!this.newTormb.date) {
          this.showErrorMsg('emptydate')
          document.getElementById('inputNewTormbDate').focus()
          return
        }

        if (this.verifyDate(this.newTormb.date)) {

          if (this.isSignIned) {

            this.isAddingItem = true

            this.$store.commit('addTormb', {
              content: this.newTormb.content,
              date: this.newTormb.date,
              is_annual: this.newTormb.is_annual
            })

            this.getTormbItems()
            //
            // this.tormbItems = this.$store.state.currentTormb

            this.isAddingItem = false;

            this.cancelAdd();

          } else { // 没登录时另外储存

            this.newTormb.tormb_id = generateId('_tormbIdMax')
            this.tormbItems.push(this.newTormb)
            localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.tormbItems))

            this.cancelAdd();
          }
        } else {
          this.showErrorMsg('invaliddate');
          document.getElementById('inputNewTormbDate').focus();
        }
      },

      cancelAdd: function () {
        this.newTormb = {
          tormb_id: 0,
          content: '',
          date: '',
          is_annual: false
        };
        this.isAddingSeen = false;
      },

      showAddingForm: function () {
        this.isAddingSeen = true;
        setTimeout(function () {
          document.getElementById('inputNewTormbContent').focus();
        }, 0);
      },

      editTormb: function (tormb) {

        // this.beforeEditCache.content = tormb.content;
        // this.beforeEditCache.date = tormb.date;
        // this.beforeEditCache.is_annual = tormb.is_annual;
        this.beforeEditCache = {...tormb}

        this.editingItem = tormb;
      },

      cancelEdit: function (tormb) {

        // tormb.content = this.beforeEditCache.content;
        // tormb.date = this.beforeEditCache.date;
        // tormb.is_annual = this.beforeEditCache.is_annual;
        // tormb = {...this.beforeEditCache}
        Object.assign(tormb, this.beforeEditCache)

        this.editingItem = null;
      },

      doneEdit: function (tormb) {

        if (!this.editingItem) {
          return;
        }
        if (!tormb.content) {
          this.showErrorMsg('emptycontent');
          return;
        }
        if (!tormb.date) {
          this.showErrorMsg('emptydate');
          return;
        }

        if (this.verifyDate(tormb.date)) {

          if (this.isSignIned) {

            this.$store.commit('editTormb', {
              tormb_id: this.editingItem.tormb_id,
              content: this.editingItem.content,
              date: this.editingItem.date,
              is_annual: this.editingItem.is_annual
            })

            this.editingItem = null

            this.getTormbItems()
            // this.tormbItems = this.$store.state.currentTormb

            // this.editingItem = null

            this.cancelEdit(tormb)

//                   if (res.data.status.code === 0) {
//                     let data = res.data.data;
//                     tormb.content = data.content;
//                     tormb.date = data.date;
//                     tormb.is_annual = !!data.is_annual;
//                     this.editingItem = null;
//                   }

          } else {
            localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.tormbItems));
            this.editingItem = null;
          }
        } else {
          this.showErrorMsg('invaliddate');
        }
      },

      // 更新每年提醒的纪念日的日期
      // recycleAnnualItem: function (tormb) {
      //
      //   let ymdArr = tormb.date.split('-', 3);
      //   ymdArr[0]++;
      //   let newDate = ymdArr.join('-');
      //
      //   if (this.isSignIned) {
      //
      //     // let requestData = qs.stringify({
      //     //   tormb_id: tormb.tormb_id,
      //     //   date: newDate,
      //     //   user_id: this.$store.state.auth.user_id
      //     // });
      //
      //     // axios
      //     // .post(
      //     //   '/api/tormb_recycle.php',
      //     //   requestData
      //     // )
      //     // .then(
      //     //   (res) => {
      //     //     if (res) {
      //     //       if (res.data.status.code === 0) {
      //     //         tormb.date = newDate;
      //     //       } else {
      //     //         this.showErrorMsg(res.data.status.code);
      //     //       }
      //     //     } else {
      //     //       this.showErrorMsg('nores');
      //     //     }
      //     //   }
      //     // )
      //     // .catch(
      //     //   (error) => {
      //     //     this.showErrorMsg('other', error);
      //     //   }
      //     // );
      //
      //   } else {
      //     tormb.date = newDate;
      //     localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.tormbItems));
      //   }
      // },

      // 删除
      removeItem: function (index) {

        if (this.isSignIned) {

          this.$store.commit('removeTormb', {
            tormb_id: this.tormbItems[index]['tormb_id']
          })

          this.getTormbItems()

        } else {
          this.tormbItems.splice(index, 1);
          window.localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.tormbItems));
        }
      },

      // 计算纪念日与当前时间相差天数
      getDaysDiff: function (date) {
        const today = new Date()
        return dayjs(date).diff(today, 'day')
      },

      verifyDate: function (date) {

        const DATE_PATTERN = /^(19[0-9][0-9]|20[0-9][0-9])-(0[0-9]|1[0-2])-(0[0-9]|[1-2][0-9]|3[01])$/g;

        if (DATE_PATTERN.test(date)) {

          let ymdArr = date.split('-', 3);
          let year = ymdArr[0]
          let month = ymdArr[1]
          let day = ymdArr[2]

          if ((month === '01' || month === '03' || month === '05' || month === '07' || month === '08' || month === '10' || month === '12') && day > 31) {
            return false;
          } else if ((month === '04' || month === '06' || month === '09' || month === '11') && day > 30) {
            return false;
          } else if (month === '02') {
            let isleap = (year % 4 === 0 && (year % 100 !== 0 || year % 400 === 0))
            if (day > 29 || (day === '29' && !isleap)) {
              return false;
            }
          }

          return true;

        } else {
          return false;
        }
      },

      moveFocus: function () {
        document.getElementById('inputNewTormbDate').focus();
      },

      showErrorMsg: function(errorCode, error) {
        if (!errorCode) {return;}
        this.errorMsg.hasError = true;
        if (error) {
          this.errorMsg.text = error.toString();
        } else {
          this.errorMsg.text = this.errorMsg.map[errorCode] + '(错误码：' + errorCode + ')';
        }
      },

      dismissErrorMsg: function() {
        this.errorMsg.hasError = false
      }
    },

    created: function () {

      // this.getTormbItems(this.$store.state.currentUser.user_id);
      this.getTormbItems()

    }
  }
</script>

<style lang="scss" scoped>

label {
  cursor: pointer;
}

.tormb-item-control {
  float: right;
  .tormb-item-edit {
    margin-right: 10px;
  }
  a:hover {
    text-decoration: none;
  }
}

#tormb-add {

  margin-bottom: 30px;

  #tormb-add-btn {
    span:first-of-type {
      cursor: pointer;
    }
  }
  #tormb-add-form {
    padding: 12px 20px;
    background: #fff;
    label {
      cursor: pointer;
    }
  }
}

.tormb-item-control a {
  cursor: pointer;
}

