<template>
  <b-modal
    id="sign-up-modal"
    title="注册"
    tabindex="-1"
  >
    <b-form @submit.stop.prevent v-show="!isRegisterSucceeded">

      <!-- 错误信息 -->
      <b-alert variant="danger" :show="errorMsg.hasError">
        {{ errorMsg.text }}
      </b-alert>

      <b-form-group
        id="register-form"
        label="邮箱"
        label-for="input-register-email"
      >
        <b-form-input
          id="input-register-email"
          type="email"
          required
          maxlength="50"
          autocomplete="off"
          :state="registerData.email.validationState"
          v-model="registerData.email.value"
          v-on:input="verifyEmail(true)"
        ></b-form-input>
        <b-form-invalid-feedback :state="registerData.email.validationState">
          请输入正确的邮箱
        </b-form-invalid-feedback>

      <!-- input username -->
      <b-form-group
        label="昵称"
        label-for="input-register-username"
      >
        <b-form-input
          id="input-register-username"
          required
          maxlength="12"
          v-model="registerData.username.value"
          :state="registerData.username.validationState"
          v-on:input="verifyUsername(true)"
        ></b-form-input>
        <b-form-invalid-feedback :state="registerData.username.validationState">
          正确的昵称为2至8位的汉字、字母、数字和“-”
        </b-form-invalid-feedback>
      </b-form-group>
        <!-- input password -->
      <b-form-group
        label="密码"
        label-for="input-register-pass"
      >
        <b-form-input
          id="input-register-pass"
          type="password"
          required
          maxlength="16"
          v-model="registerData.password_first.value"
          :state="registerData.password_first.validationState"
          v-on:input="verifyPasswordFirst(true)"
        ></b-form-input>
        <b-form-invalid-feedback :state="registerData.password_first.validationState">
          正确的密码为6至16的字母、数字和下划线
        </b-form-invalid-feedback>
      </b-form-group>
        <!-- confirm password -->
      <b-form-group
        label="确认密码"
        label-for="input-register-confirmation-pass"
      >
<!--              <div class="col-sm-10">-->
        <b-form-input
          id="input-register-confirmation-pass"
          type="password"
          name="pass_second"
          required
          maxlength="16"
          v-model="registerData.password_second.value"
          :state="registerData.password_second.validationState"
          v-on:input="verifyPasswordSecond(true)"
        ></b-form-input>
        <b-form-invalid-feedback :state="registerData.password_second.validationState">
          两次输入的密码不一样
        </b-form-invalid-feedback>
      </b-form-group>
      <!-- input full name -->
      <b-form-group
        label="姓名"
        label-for="input-lastname"
      >
        <b-input-group
          prepend="选填"
        >
          <b-form-input
            id="input-lastname"
            name="last_name"
            placeholder="姓氏"
            maxlength="12"
            v-model="registerData.lastname.value"
            :state="registerData.lastname.validationState"
            v-on:input="verifyLastname(true)"
          ></b-form-input>
          <b-form-invalid-feedback :state="registerData.lastname.validationState"></b-form-invalid-feedback>
        </b-input-group>
        <b-input-group
          prepend="选填"
        >
          <b-form-input
            id="input-firstname"
            name="first_name"
            placeholder="名字"
            maxlength="12"
            v-model="registerData.firstname.value"
            :state="registerData.firstname.validationState"
            v-on:input="verifyFirstname(true)"
          ></b-form-input>
          <b-form-invalid-feedback :state="verifyFullName()">
            正确的姓氏和名字各为1至12位汉字、字母和“-”，需全填或全空
          </b-form-invalid-feedback>
        </b-input-group>
      </b-form-group>
      <!-- choose avatar -->
      <div class="avatar-box">
        <p>请选择头像</p>
        <img
          v-for="avatar in avatarData.avatars"
          :key="avatar"
          :src="getAvatarUrl(avatar)"
          :alt="avatar"
          :class="avatar === avatarSelected ? 'selected': ''"
          :title="avatar"
          v-on:click.prevent="selectAvatar(avatar)"
        >
      </div>
      <input
        id="input-avatar-url"
        type="hidden"
        name="avatar_url"
        v-model="registerData.avatar.value"
      >
      </b-form-group>
    </b-form>


    <!-- 注册成功的提示 -->
    <p class="text-center" v-show="isRegisterSucceeded">
      <strong>注册成功</strong>
      <br>
      2 秒后自动用注册信息登入
    </p>
<!--    <div class="alert alert-success" role="alert" v-show="isRegisterSucceeded">-->
<!--      <strong>注册成功</strong>&nbsp;&nbsp;2 秒后自动用注册信息登入-->
<!--    </div>-->

    <template #modal-footer="{cancel}">
      <b-button @click="cancel()">
        取消
      </b-button>
      <b-button
        variant="success"
        id="register-submit-btn"
        :disabled="isSubmitting"
        @click="submitRegisterForm"
      >
        注册
      </b-button>
    </template>
  </b-modal><!-- /.modal -->
</template>

<script>

export default {
  name: 'sign-up-modal',

  data: function() {
    return {

      registerData: {
        email: {
          value: '',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: /^[a-z][a-z0-9_]{1,30}@.+(\.[a-z]{2,3})+$/,
          timer: null
        },
        username: {
          value: '',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: /^[\u4e00-\u9fa5a-zA-z0-9-]{2,8}$/,
          timer: null
        },
        password_first: {
          value: '',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: /^[a-zA-Z0-9_]{6,16}$/,
          timer: null
        },
        password_second: {
          value: '',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: /^[a-zA-Z0-9_]{6,16}$/,
          timer: null
        },
        lastname: {
          value: '',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: /^[\u4e00-\u9fa5a-zA-z-]{1,12}$/,
          timer: null
        },
        firstname: {
          value: '',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: /^[\u4e00-\u9fa5a-zA-z-]{1,12}$/,
          timer: null
        },
        avatar: {
          value: 'asn',
          validationState: null, // 'has-success' || 'has-error' || ''
          regex: null,
          timer: null
        }
      },

      avatarData: {
        baseURL: './assets/',
        avatars: [
          'asn',
          'beauty',
          'bojack',
          'cherry',
          'doge',
          'doraemon',
          'frog'
        ]
      },

      avatarSelected: 'asn',

      isSubmitting: false,

      isRegisterSucceeded: false,

      errorMsg: {
        hasError: false,
        text: '',
        map: {
          100: '注册失败，写入数据库时发生错误',
          200: '该邮箱已被使用',
          300: '注册失败，提交的数据有误',
          // no correct response
          'nores': '服务器发生错误',
          'signined': '请登出后再注册',
          'invalidsignupdata': '你的注册信息没有填写正确',
          // other
          'other': ''
        }
      }

    }
  },

  computed: {
    isSignIned: function() {
      return this.$store.state.isSignIned;
    }
  },

  methods: {

    getAvatarUrl(avatar) {
      return require('@/assets/avatar/' + avatar + '_50.jpg')
    },

    submitRegisterForm: function() {

      if (this.isSubmitting) {
        return;
      }
      if (this.isSignIned) {
        this.showErrorMsg('signined');
        return;
      }

      this.dismissErrorMsg();

      // 验证所有需要提交的数据
      if (this.verifyRegisterData()) {

        this.isSubmitting = true;

        // let requestData = qs.stringify({
        //   email: this.registerData.email.value,
        //   user_name: this.registerData.username.value,
        //   pass1: this.registerData.password_first.value,
        //   pass2: this.registerData.password_second.value,
        //   last_name: this.registerData.lastname.value,
        //   first_name: this.registerData.firstname.value,
        //   avatar: this.registerData.avatar.value
        // });
        // console.log('beforeCommit')
        this.$store.commit('signUp', {
          email: this.registerData.email.value,
          user_name: this.registerData.username.value,
          password: this.registerData.password_first.value,
          last_name: this.registerData.lastname.value,
          first_name: this.registerData.firstname.value,
          avatar: this.registerData.avatar.value
        })

        this.isSubmitting = false

        if (this.$store.state.errorState.signUpExistedEmail) {
          this.showErrorMsg(200)
        } else {
          this.isRegisterSucceeded = true
          setTimeout(() => {
            location.reload(true)
          },2000)
        }
        // console.log('afterCommit')
        // location.reload(true)


        // .then(
        //   (res) => {
        //     if (res) {
        //       if (res.data.status.code === 0) {
        //         let userdata = res.data.data;
        //         this.$store.commit('signin', userdata);
        //         this.isSubmitting = false;
        //         this.isRegisterSucceeded = true;
        //         setTimeout(() => {
        //           location.reload(true);
        //         },2000);
        //       } else {
        //         this.showErrorMsg(res.data.status.code);
        //       }
        //     } else {
        //       this.showErrorMsg('nores');
        //     }
        //     this.isSubmitting = false;
        //   }
        // )
        // .catch(
        //   (error) => {
        //     this.isSubmitting = false;
        //     this.showErrorMsg('other', error);
        //   }
        // );

      } else {
        this.showErrorMsg('invalidsignupdata');
      }

    },

    verifyEmail: function(isDebounced) {

      const email = this.registerData.email;

      if (email.value === '') {
        email.validationState = '';
        if (email.timer) {
          clearTimeout(email.timer);
        }
        return false;
      }

      if (isDebounced) {       // if set timeout
        if (email.timer) {
          clearTimeout(email.timer);
        }
        email.timer = setTimeout(
          () => {
            if (email.regex.test(email.value)) {
              email.validationState = true;
              return true;
            } else {
              email.validationState = false;
              return false;
            }
          },
          750
        );
      } else {
        return email.regex.test(email.value);
      }
    },

    verifyUsername: function(isDebounced) {

      const username = this.registerData.username;

      if (username.value === '') {
        username.validationState = '';
        if (username.timer) {
          clearTimeout(username.timer);
        }
        return false;
      }

      if (isDebounced) {       // if set timeout
        if (username.timer) {
          clearTimeout(username.timer);
        }
        username.timer = setTimeout(
          () => {
            if (username.regex.test(username.value)) {
              username.validationState = true;
              return true;
            } else {
              username.validationState = false;
              return false;
            }
          },
          750
        );
      } else {
        return username.regex.test(username.value);
      }
    },

    verifyPasswordFirst: function(isDebounced) {

      const password_first = this.registerData.password_first;

      if (password_first.value === '') {
        password_first.validationState = '';
        if (password_first.timer) {
          clearTimeout(password_first.timer);
        }
        return false;
      }

      if (isDebounced) {       // if set timeout
        if (password_first.timer) {
          clearTimeout(password_first.timer);
        }
        password_first.timer = setTimeout(
          () => {
            if (password_first.regex.test(password_first.value)) {
              password_first.validationState = true;
              return true;
            } else {
              password_first.validationState = false;
              return false;
            }
          },
          750
        );
      } else {
        return password_first.regex.test(password_first.value);
      }
    },

    verifyPasswordSecond: function(isDebounced) {

      const password_second = this.registerData.password_second;
      const password_first = this.registerData.password_first;

      if (password_second.value === '') {
        password_second.validationState = '';
        if (password_second.timer) {
          clearTimeout(password_second.timer);
        }
        return false;
      }

      if (isDebounced) {       // if set timeout
        if (password_second.timer) {
          clearTimeout(password_second.timer);
        }
        password_second.timer = setTimeout(
          () => {
            if (password_second.regex.test(password_second.value) && password_second.value === password_first.value) {
              password_second.validationState = true;
              return true;
            } else {
              password_second.validationState = false;
              return false;
            }
          },
          750
        );
      } else {
        return password_second.regex.test(password_second.value) && password_second.value === password_first.value;
      }
    },

    verifyFirstname: function(isDebounced) {

      const firstname = this.registerData.firstname;

      if (firstname.value === '') {
        firstname.validationState = null;
        if (firstname.timer) {
          clearTimeout(firstname.timer);
        }
        return false;
      }

      if (isDebounced) {       // if set timeout
        if (firstname.timer) {
          clearTimeout(firstname.timer);
        }
        firstname.timer = setTimeout(
          () => {
            if (firstname.regex.test(firstname.value)) {
              firstname.validationState = true;
              return true;
            } else {
              firstname.validationState = false;
              return false;
            }
          },
          750
        );
      } else {
        return firstname.regex.test(firstname.value);
      }
    },

    verifyLastname: function(isDebounced) {

      const lastname = this.registerData.lastname;

      if (lastname.value === '') {
        lastname.validationState = null;
        if (lastname.timer) {
          clearTimeout(lastname.timer);
        }
        return false;
      }

      if (isDebounced) {       // if set timeout
        if (lastname.timer) {
          clearTimeout(lastname.timer);
        }
        lastname.timer = setTimeout(
          () => {
            if (lastname.regex.test(lastname.value)) {
              lastname.validationState = true;
              return true;
            } else {
              lastname.validationState = false;
              return false;
            }
          },
          750
        );
      } else {
        return lastname.regex.test(lastname.value);
      }
    },

    verifyFullName: function() {
      if (this.registerData.lastname.value === '' && this.registerData.firstname.value === '') {
        return true;
      } else {
        return this.verifyFirstname() && this.verifyLastname();
      }
    },

    verifyAvatar: function() {
      return this.avatarData.avatars.indexOf(this.registerData.avatar.value) >= 0;
    },

    verifyRegisterData: function() {
      return this.verifyEmail() &&
      this.verifyUsername() &&
      this.verifyPasswordFirst() &&
      this.verifyPasswordSecond() &&
      this.verifyFullName() &&
      this.verifyAvatar();
    },

    selectAvatar: function(avatar) {
      this.avatarSelected = avatar;
      this.registerData.avatar.value = avatar;
    },

    showErrorMsg: function(errorcode, error) {
      if (!errorcode) {return;}
      this.errorMsg.hasError = true;
      if (error) {
        this.errorMsg.text = error.toString();
      } else {
        this.errorMsg.text = this.errorMsg.map[errorcode] + '(错误码：' + errorcode + ')';
      }
    },

    dismissErrorMsg: function() {
      if (this.errorMsg.hasError === false) {return;}
      this.errorMsg.hasError = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.avatar-box {
  text-align: center;
  margin-bottom: 30px;
  img {
    margin: 0 3px;
    border-radius: 5px;
    display: inline-block;
    border: 2px solid rgba(255, 255, 255, 0);
    &:hover {
       cursor: pointer;
       opacity: 0.7;
       border: 2px solid #f60;
    }
    &.selected {
       cursor: pointer;
       border: 2px solid #f60;
    }
  }
}

.help {
  visibility: hidden;
  &.has-error {
    visibility: visible;
  }
}
</style>
