<template>
  <b-modal
    id="sign-in-modal"
    title="登录"
    tabindex="-1"
    v-if="!isSignIned"
  >
    <b-alert variant="danger" :show="errorMsg.hasError">
      {{errorMsg.text}}
    </b-alert>

    <b-form @submit="submitSignInData">
      <b-form-group
        label="邮箱："
        label-for="inputLoginEmail"
      >
        <b-form-input
          id="inputLoginEmail"
          type="email"
          placeholder="请输入邮箱"
          required
          maxlength="50"
          v-model="loginData.email"
        ></b-form-input>
      </b-form-group>
      <b-form-group
        label="密码："
        label-for="inputLoginPassword"
      >
        <b-form-input
          id="inputLoginPassword"
          type="password"
          placeholder="密码"
          required
          maxlength="16"
          v-model="loginData.password"
          v-on:keyup.enter="submitSignInData"
        ></b-form-input>
      </b-form-group>
    </b-form>
    <template #modal-footer="{cancel}">
      <b-button @click="cancel()">
        取消
      </b-button>
      <b-button
        variant="success"
        :disabled="isSubmitting"
        @click.prevent="submitSignInData"
      >
        登录
      </b-button>
    </template>
  </b-modal>
</template>

<script>

export default {

  name: 'sign-in-modal',

  data: function() {

    return {

      loginData: {
        email: '',
        password: ''
      },

      isSubmitting: false,

      errorMsg: {
        hasError: false,
        text: '',
        map: {
          100: '登录失败，该用户不存在',
          200: '登录失败，密码错误',
          'signined': '你已经登录了',
          'invalid-sign-in-data': '你的登录信息没有填写完整'
        }
      }

    }
  },

  computed: {
    isSignIned: function() {
      return this.$store.state.isSignIned;
    }
  },

  methods: {

    submitSignInData: function() {

      this.$store.commit('clearErrorState')

      if (this.isSubmitting) {
        return;
      }

      if (this.isSignIned) {
        this.showErrorMsg('signined');
        return;
      }

      this.dismissErrorMsg();

      if (this.loginData.email && this.loginData.password) {

        this.isSubmitting = true;

        this.$store.commit('signIn', this.loginData)

        this.isSubmitting = false

        if (this.$store.state.errorState.signInNoMatchEmail) {
          this.showErrorMsg(100)
        } else if (this.$store.state.errorState.signInWrongPassword) {
          this.showErrorMsg(200)
        } else {
          location.reload(true)
        }
      } else {
        this.showErrorMsg('invalid-sign-in-data');
      }
    },

    showErrorMsg: function(errorCode, error) {
      if (!errorCode) {return}
      this.errorMsg.hasError = true
      if (error) {
        this.errorMsg.text = error.toString()
      } else {
        this.errorMsg.text = this.errorMsg.map[errorCode] + '(错误码：' + errorCode + ')'
      }
    },

    dismissErrorMsg: function() {
      this.errorMsg.hasError = false
    }
  }
}
</script>

