import Vue from 'vue'
import Vuex from 'vuex'
import {generateId} from '@/helper/helper'
import dayjs from 'dayjs'

Vue.use(Vuex)

const USER_KEY = 'user_db'
const TORMB_KEY = 'tormb_db'
const TODO_KEY = 'todo_db'
const MSG_KEY = 'msg_db'

export default new Vuex.Store({
  state: {
    isSignIned: false,
    currentUser: {
      user_id: -1,       // not signined: -1
      user_name: '',     // not signined: ''
      last_name: '',
      first_name: '',
      avatar: '',
      user_level: -1     // not signined: -1
    },
    currentTormb: [],
    currentTodo: [],
    currentMsg: [],
    allMsgLength: 0,
    errorState: {
      signInNoMatchEmail: false,
      signInWrongPassword: false,
      signUpExistedEmail: false,
      removeMsgWrongUserID: false,
      removeMsgWrongMsgID: false
    }
  },
  mutations: {
    // 初始化数据库
    initDB() {
      if (window.localStorage.getItem(USER_KEY) === null) {
        window.localStorage.setItem(USER_KEY, JSON.stringify([{
          user_id: 9999,
          email: 'admin1@admin.com',
          user_name: '牛大虾',
          password: '123456',
          first_name: '大虾',
          last_name: '牛',
          avatar: 'doge',
          user_level: 0
        }, {
          user_id: 9998,
          email: 'admin2@admin.com',
          user_name: '牛春花',
          password: '123456',
          first_name: '春花',
          last_name: '牛',
          avatar: 'cherry',
          user_level: 0
        }]))
      }
      if (window.localStorage.getItem(TORMB_KEY) === null) {
        window.localStorage.setItem(TORMB_KEY, JSON.stringify([]))
      }
      if (window.localStorage.getItem(TODO_KEY) === null) {
        window.localStorage.setItem(TODO_KEY, JSON.stringify([]))
      }
      if (window.localStorage.getItem(MSG_KEY) === null) {
        window.localStorage.setItem(MSG_KEY, JSON.stringify([{
          msg_id: generateId('_msgIdMax'),
          user_id: 9998,
          content: '欢迎来到留言板，注册登录之后才可以留言哦',
          user_name: '牛春花',
          avatar: 'cherry',
          post_time: dayjs((new Date())).format('YYYY-MM-DD HH:mm:ss')
        }, {
          msg_id: generateId('_msgIdMax'),
          user_id: 9999,
          content: '也可以登录我的管理员账号，还可以删除其他人的留言。\n邮箱：admin1@admin.com\n密码：123456',
          user_name: '牛大虾',
          avatar: 'doge',
          post_time: dayjs((new Date())).format('YYYY-MM-DD HH:mm:ss')
        }]))
      }
      if (window.localStorage.getItem('isSignIned') === null) {
        window.localStorage.setItem('isSignIned', JSON.stringify(false))
      }
      if (window.localStorage.getItem('current_user') === null) {
        window.localStorage.setItem('current_user', JSON.stringify(
          {
            user_id: -1,
            user_name: '',
            last_name: '',
            first_name: '',
            avatar: '',
            user_level: -1
          }
        ))
      }
    },

    setSignInTrue(state) {
      state.isSignIned = true
    },

    signInWithoutPassword(state, payload) {
      state.currentUser = {
        user_id: payload.user_id,
        user_name: payload.user_name,
        last_name: payload.last_name,
        first_name: payload.first_name,
        avatar: payload.avatar,
        user_level: payload.user_level
      }
      window.localStorage.setItem('current_user', JSON.stringify(state.currentUser))
      window.localStorage.setItem('isSignIned', JSON.stringify(true))
      state.isSignIned = true
    },

    signIn(state, payload){
      // 清除旧的错误状态
      state.errorState.signInNoMatchEmail = false
      state.errorState.signInWrongPassword = false

      const userArr = JSON.parse(window.localStorage.getItem(USER_KEY))
      let u = {}
      let wrongPassword = false
      let noEmail = true
      for (let i = 0; i < userArr.length; i++) {
        const current = userArr[i]
        if (current.email === payload.email) {
          noEmail = false
          if (current.password === payload.password) {
            wrongPassword = false
            u = {
              user_id: current.user_id,
              user_name: current.user_name,
              last_name: current.last_name,
              first_name: current.first_name,
              avatar: current.avatar,
              user_level: current.user_level
            }
          } else {
            wrongPassword = true
          }
          break
        }
      }
      if (wrongPassword) {
        state.errorState.signInWrongPassword = true
        return
      }
      if (noEmail) {
        state.errorState.signInNoMatchEmail = true
        return
      }
      window.localStorage.setItem('current_user', JSON.stringify(u))
      window.localStorage.setItem('isSignIned', JSON.stringify(true))
      state.currentUser = u
      state.isSignIned = true
    },

    signOut(state) {
      const u = {
        user_id: -1,
        user_name: '',
        last_name: '',
        first_name: '',
        avatar: '',
        user_level: -1
      }
      state.currentUser = u
      window.localStorage.setItem('current_user', JSON.stringify(u))
      window.localStorage.setItem('isSignIned', JSON.stringify(false))
      state.isSignIned = false
    },

    signUp(state, payload) {
      // 清除旧的错误状态
      state.errorState.signUpExistedEmail = false

      const userArr = JSON.parse(window.localStorage.getItem(USER_KEY))
      let existedEmail = false
      for (let i = 0; i < userArr.length; i++) {
        if (userArr[i].email === payload.email) {
          existedEmail = true
          break
        }
      }
      if (existedEmail) {
        state.errorState.signUpExistedEmail = true
        return
      }
      const newUser = {
        user_id: generateId('_userIdMax'),
        email: payload.email,
        user_name: payload.user_name,
        password: payload.password,
        last_name: payload.last_name,
        first_name: payload.first_name,
        avatar: payload.avatar,
        user_level: 1
      }

      userArr.push(newUser)
      window.localStorage.setItem(USER_KEY, JSON.stringify(userArr))
      // sign in
      state.currentUser = {
        user_id: newUser.user_id,
        user_name: newUser.user_name,
        last_name: newUser.last_name,
        first_name: newUser.first_name,
        avatar: newUser.avatar,
        user_level: newUser.user_level
      }
      window.localStorage.setItem('current_user', JSON.stringify(state.currentUser))
      window.localStorage.setItem('isSignIned', JSON.stringify(true))
      state.isSignIned = true
      // state.$store.commit('signInWithoutPassword', newUser)
    },

    clearErrorState(state) {
      for (let key in state.errorState) {
        state.errorState[key] = false
      }
    },

    // Tormb App

    getTormb(state, payload) {
      const tormbArr = JSON.parse(window.localStorage.getItem(TORMB_KEY) || '[]')
      state.currentTormb = tormbArr.filter((t) => t.user_id === payload.user_id)
    },

    addTormb(state, payload) {
      const tormbArr = JSON.parse(window.localStorage.getItem(TORMB_KEY))
      tormbArr.push({
        tormb_id: generateId('_tormbIdMax'),
        user_id: state.currentUser.user_id,
        content: payload.content,
        date: payload.date,
        is_annual: payload.is_annual
      })
      window.localStorage.setItem(TORMB_KEY, JSON.stringify(tormbArr))
      state.currentTormb = tormbArr
    },

    editTormb(state, payload) {
      const tormbArr = JSON.parse(window.localStorage.getItem(TORMB_KEY))
      if (tormbArr.length === 0) {return}
      const tormb = tormbArr.filter((t) => t.tormb_id === payload.tormb_id)[0]
      if (!tormb) {return}
      tormb.content = payload.content
      tormb.date = payload.date
      tormb.is_annual = payload.is_annual
      window.localStorage.setItem(TORMB_KEY, JSON.stringify(tormbArr))
      state.currentTormb = tormbArr
    },

    removeTormb(state, payload) {
      const tormbArr = JSON.parse(window.localStorage.getItem(TORMB_KEY))
      const idArr = tormbArr.map((t) => t.tormb_id)
      const index = idArr.indexOf(payload.tormb_id)
      tormbArr.splice(index, 1)
      window.localStorage.setItem(TORMB_KEY, JSON.stringify(tormbArr))
      state.currentTormb = tormbArr
    },

    // To_do App

    getTodo(state, payload) {
      const todoArr = JSON.parse(window.localStorage.getItem(TODO_KEY) || '[]')
      state.currentTodo = todoArr.filter((t) => t.user_id === payload.user_id)
    },

    addTodo(state, payload) {
      const todoArr = JSON.parse(window.localStorage.getItem(TODO_KEY))
      todoArr.push({
        todo_id: generateId('_todoIdMax'),
        user_id: state.currentUser.user_id,
        content: payload.content,
        is_complete: false
      })
      window.localStorage.setItem(TODO_KEY, JSON.stringify(todoArr))
      state.currentTodo = todoArr
    },

    editTodo(state, payload) {
      const todoArr = JSON.parse(window.localStorage.getItem(TODO_KEY))
      if (todoArr.length === 0) {return}
      const todo = todoArr.filter((t) => t.todo_id === payload.todo_id)[0]
      if (!todo) {return}
      todo.content = payload.content
      // to.do.is_complete = payload.is_complete
      window.localStorage.setItem(TODO_KEY, JSON.stringify(todoArr))
      state.currentTodo = todoArr
    },

    toggleTodo(state, payload) {
      const todoArr = JSON.parse(window.localStorage.getItem(TODO_KEY))
      if (todoArr.length === 0) {return}
      const todo = todoArr.filter((t) => t.todo_id === payload.todo_id)[0]
      if (!todo) {return}
      todo.is_complete = payload.is_complete
      window.localStorage.setItem(TODO_KEY, JSON.stringify(todoArr))
      state.currentTodo = todoArr
    },

    removeTodo(state, payload) {
      const todoArr = JSON.parse(window.localStorage.getItem(TODO_KEY))
      const idArr = todoArr.map((t) => t.todo_id)
      const index = idArr.indexOf(payload.todo_id)
      todoArr.splice(index, 1)
      window.localStorage.setItem(TODO_KEY, JSON.stringify(todoArr))
      state.currentTodo = todoArr
    },

    // Chatboard App

    getMsg(state, payload) {
      const msgArr = JSON.parse(window.localStorage.getItem(MSG_KEY) || '[]')
      const start = Number.parseInt(payload.start)
      const length = Number.parseInt(payload.display)
      state.allMsgLength = msgArr.length
      state.currentMsg = msgArr.slice(start, start + length)
    },

    addMsg(state, payload) {
      const msgArr = JSON.parse(window.localStorage.getItem(MSG_KEY) || '[]')
      msgArr.unshift({
        msg_id: generateId('_msgIdMax'),
        user_id: state.currentUser.user_id,
        user_name: state.currentUser.user_name,
        content: payload.content,
        post_time: dayjs((new Date())).format('YYYY-MM-DD HH:mm:ss'),
        avatar: state.currentUser.avatar
      })
      window.localStorage.setItem(MSG_KEY, JSON.stringify(msgArr))
      state.currentMsg = msgArr
    },

    removeMsg(state, payload) {
      // 清除错误信息
      state.errorState.removeMsgWrongUserID = false
      state.errorState.removeMsgWrongMsgID = false

      const msgArr = JSON.parse(window.localStorage.getItem(MSG_KEY) || '[]')
      const indexArr = msgArr.map((m) => m.msg_id)
      const index = indexArr.indexOf(payload.msg_id)

      if (index > -1) {
        // msg 是当前用户发布或当前用户是管理员时，可执行删除
        if (msgArr[index].user_id === state.currentUser.user_id || state.currentUser.user_level === 0) {
          msgArr.splice(index, 1)
          window.localStorage.setItem(MSG_KEY, JSON.stringify(msgArr))
          state.currentMsg = msgArr
        } else {
          // 不是当前用户的 msg
          state.errorState.removeMsgWrongUserID = true
        }
      } else {
        // 找不到对应 msg_id
        state.errorState.removeMsgWrongMsgID = true
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
